import { createPortal } from "react-dom";
import { Context } from "./Context";
import { _useModal, _useModalPortal } from "../Hooks";
import { ModalTypes } from '../Types';

export const Provider: ModalTypes['Parts']['Provider'] = ({ children }) => {
  const modalState = _useModal();
  const modalRoot  = _useModalPortal()

  return modalRoot ? (
    createPortal(
      <Context.Provider value={modalState}>
        {children}
      </Context.Provider>,
      modalRoot
    )
  ) : null;
};